<script>
import { Doughnut } from 'vue-chartjs'
export default {
  extends: Doughnut,
  props: ['name', 'data'],
  mounted() {
    this.renderDonughtChart()
  },
  computed: {
    chartData: function () {
      return this.data
    },
  },
  methods: {
    renderDonughtChart: function () {
      this.renderChart(
        {
          // labels: ["Remaining", "Used"],
          datasets: [
            {
              borderWidth: 0,
              label: this.name,
              backgroundColor: ['#00D1B2', '#fffff'],
              data: this.data,
            },
          ],
        },
        {
          legend: {
            display: true,
            labels: {
              fontColor: '#F1F6F9',
              fontSize: 14,
            },
          },
          cutoutPercentage: 80,
          responsive: true,
          maintainAspectRatio: false,
        }
      )
    },
  },
  watch: {
    data: function() {
      this.renderDonughtChart().destroy()   // destroy()
      this.renderLineChart()  // destroyrender
    }
  }
}

